/*
Base Rules: The default styles.
*/
::-webkit-scrollbar {
  display: none; }

body {
  color: #000;
  font-family: "GothamRounded-Book";
  position: relative;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-family: "GothamRounded-Bold";
  line-height: 1; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

b {
  color: #000;
  font-family: "GothamRounded-Book";
  font-weight: 500; }

a {
  text-decoration: none; }
  a:hover, a:focus {
    text-decoration: none; }

p {
  margin: 0;
  color: #000;
  font-family: "GothamRounded-Book";
  font-range: 320px 1920px;
  font-size: responsive 16px 18px;
  line-height: 1.8; }
  @media screen and (min-width: 992px) {
    p {
      font-size: 20px; } }

svg {
  height: auto; }

strong {
  font-family: "GothamRounded-Book"; }

/*
    Module Rules make up the smaller, groupable areas of the site.

    Some examples are callouts, calls-to-action (cta), sidebars, and so on.
    They should be named in a way that is independent of the page context, making
    them reusable throughout the site. This is the point of modularity.
*/
/*
Layout Rules: Dictate major layout components.

Layout Rules divide the page into sections and hold one or more modules
together. Wrappers, rows, columns and any grid-based styles can go here.
Also major semantic areas as well, such as header, content and footer.
*/
/**
