* -----------------------------------------------------------------------
*/
.c-white {
  color: #fff; }

.c-black {
  color: #000; }

/**
* -----------------------------------------------------------------------
*                                Background
* -----------------------------------------------------------------------
*/
.bg-white {
  background-color: #fff; }

.bg-black {
  background-color: #000; }

/**
